<template>
  <div class="dealRecord-wrap" :style="{ height: windowWidth + 'px' }" id="app">
    <div
      class="padding-top-one"
      style="display: flex; align-items: center; justify-content: center"
    >
      <img src="../../static/img/startLogo.png" style="z-index: 10" />
    </div>
    <div
      class="padding-top-two"
      style="display: flex; align-items: center; justify-content: center"
    >
      <img src="../../static/img/startPageText.png" />
    </div>
    <div
      class="padding-top-three"
      style="font-size: 15px; color: #282630; text-align: center"
    >
      {{ i18n.headHint }}
    </div>
    <div
      style="
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 57px;
      "
    >
      <img src="../../static/img/startPageImg.png" style="width: 200px; height: 297px" />
    </div>
    <!--/static/img/startPageImg.png-->
    <div class="section-wrap">
      <div class="language-wrap">
        <div
          @click="selectLang(0)"
          class="item-wrap"
          :class="headIndex == 0 ? 'head-tabindex' : 'no-head-tabindex'"
        >
          中
        </div>
        <div
          @click="selectLang(1)"
          class="item-wrap"
          :class="headIndex == 1 ? 'head-tabindex' : 'no-head-tabindex'"
        >
          En
        </div>
        <div
          @click="selectLang(2)"
          class="item-wrap"
          :class="headIndex == 2 ? 'head-tabindex' : 'no-head-tabindex'"
        >
          ไทย
        </div>
      </div>

      <div class="input-wrap">
        <input
          type="text"
          :placeholder="i18n.inputAccount"
          v-model="telephone"
          maxlength="30"
        />
      </div>
      <div
        class="input-wrap input-margin-top"
        style="justify-content: space-between; padding-right: 0"
      >
        <input
          type="text"
          :placeholder="i18n.inputCode"
          v-model="phoneYzm"
          maxlength="10"
          style="width: 60%"
        />
        <div class="get-code-style" style="" @click="getCode">
          <span v-if="!codeBtn">{{ i18n.codeHint }}</span>
          <span v-if="codeBtn">{{ CountMessage }}</span>
        </div>
      </div>
      <!-- <div class="input-wrap input-margin-top">
        <input
          type="password"
          placeholder="请输入密码"
          v-model="password"
          maxlength="20"
        />
      </div> -->
      <div
        style="
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          z-index: 20;
        "
        @click="donwload"
        class="down-text-style"
      >
        <div class="text-padding">{{ i18n.downLoadText }}</div>
      </div>
      <div class="registor-btn-wrap">
        <div class="btn" @click="userPan">{{ i18n.registorText }}</div>
      </div>
    </div>
    <div class="fixed-loading" v-if="loadingBtn">
      <van-loading size="24px" color="#D9323C" vertical>{{ i18n.loading }}</van-loading
      ><!--加载中...-->
    </div>
    <!--  -->
  </div>
</template>
<script>
// import Login from '../assets/images/loginBgOne.png';
import { sendSmsCode, register, getDownUrl } from "@/api/index.js";
import { connect } from "tls";
import { constants } from "zlib";

export default {
  data() {
    return {
      imageCode: "",

      telephone: "",
      txCode: "",
      phoneYzm: "",
      InviteCode: "",
      password: "",
      payPassword: "",

      CountMessage: "", //"获取验证码"

      imger: "",
      imageToken: "",
      timer: "",
      count: "",

      windowWidth: null,
      windowHeight: null,
      loadingBtn: false,
      downLoadUrl: "",
      lang: "zh",
      headIndex: 0,
      windowHeightD: null,

      codeBtn: false,
    };
  },
  computed: {
    i18n() {
      return this.$t("message");
    },
  },
  created() {
    // document.body.style.backgroundColor = "#f8";
    localStorage.setItem("lang", "zh");
    // console.log(localStorage.getItem("lang"));
    this.getYaoQing();
    //console.log(document.documentElement.clientWidth,document.documentElement.clientHeight)
    this.windowWidth = document.documentElement.clientHeight;
    //this.gettucode();
    this.getDownUrl();
    // window.onload = function(){ //在页面整体加载完毕时
    //   document.getElementById('#app').style.height= hrt+'px'//把获取到的高度赋值给根div
    // }
  },

  mounted() {},

  components: {},

  methods: {
    selectLang(index) {
      if (index == 0) {
        this._i18n.locale = "ch";
        localStorage.setItem("lang", "zh");
      } else if (index == 1) {
        this._i18n.locale = "en";
        localStorage.setItem("lang", "en");
      } else if (index == 2) {
        this._i18n.locale = "th";
        localStorage.setItem("lang", "th");
      }
      //console.log(localStorage.getItem("lang"));
      this.headIndex = index;
    },
    change: function () {
      this._i18n.locale = "chs";
    },
    change2: function () {
      this._i18n.locale = "en";
    },
    getCode() {
      var strTemp = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
      if (!this.telephone) {
        console.log(123);
        this.$toast(this.i18n.inputAccount); //"请输入手机号/邮箱"
        return;
      }

      // if (!strTemp.test(this.telephone)) {
      //   this.$toast("手机号格式不正确");
      //   return;
      // }
      // if (!this.txCode) {
      //   this.$toast("请输入图形验证码");
      //   return;
      // }
      this.loadingBtn = true;
      var timeCount = 60;
      if (!this.codeBtn) {
        this.loadingBtn = true;
        let params = {
          accountNumber: this.telephone,
          smsType: 1,
          areaCode: "86",
        };
        sendSmsCode(params).then((res) => {
          this.loadingBtn = false;
          if (res.status == 1) {
            this.codeBtn = true;
            this.$toast(this.i18n.codeSendSuccess); //"短信验证码已发送，请注意查收"
            if (!this.timer) {
              this.count = timeCount;
              // console.log(this.count);
              //  this.show = false;
              this.timer = setInterval(() => {
                if (this.count > 0 && this.count <= timeCount) {
                  this.count--;
                  this.CountMessage = this.i18n.Remaining + this.count + "s";
                } else {
                  //  this.show = true;
                  // this.dis = false;
                  clearInterval(this.timer);
                  // this.CountMessage = "重新获取";
                  this.codeBtn = false;
                  this.timer = null;
                }
              }, 1000);
            }
          } else {
            //this.CountMessage = "重新获取";
            this.$toast(res.message);
            //this.gettucode();
          }
        });
      }
    },
    donwload() {
      console.log(this.downLoadUrl+'?'+'lang='+localStorage.getItem("lang"));
      //window.location.href='http://192.168.3.44/fenfa'+'?'+'lang='+localStorage.getItem("lang");
      window.location.href = this.downLoadUrl+'?'+'lang='+localStorage.getItem("lang");
    }, 
    //判断数据传出数据是否正常 
    userPan() {
      var man = "";
      // /^1[3|4|5|6|7|8|9][0-9]\d{8}$/
      var strTemp = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
      var passTemp = /^[0-9A-Za-z]{8,20}$/;
      if (!this.telephone) {
        console.log(123);
        this.$toast(this.i18n.inputAccount); //"请输入手机号/邮箱"
        return;
      }

      // if (!strTemp.test(this.telephone)) {
      //   this.$toast("手机号格式不正确");
      //   return;
      // }
      // if (!this.txCode) {
      //   this.$toast("请输入图形验证码");
      //   return;
      // }
      if (!this.phoneYzm) {
        this.$toast(this.i18n.inputCode); //"请输入验证码"
        return;
      }
      // if (!this.password) {
      //   this.$toast("请输入密码");
      //   return;
      // }
      // if (!this.payPassword) {
      //   this.$toast("请输入支付密码");
      //   return;
      // }
      this.loadingBtn = true;
      this.setLogin();
    },
    //注册接口
    setLogin() {
      let _data = {
        mobile: this.telephone,
        password: this.password,
        rePassword: this.password,
        inviteCode: this.InviteCode,
        code: this.phoneYzm,
      };
      console.log(_data);
      register(_data).then((res) => {
        console.log(res);
        this.loadingBtn = false;
        if (res.status == 1) {
          this.$toast(this.i18n.registorSuccess); //"注册成功"
          setTimeout(() => {
            window.location.href = this.downLoadUrl;
          }, 1000);
        } else {
          //  window.location.href = 'https://api.tgp125.cn/cy/ffpt/index.html'
          this.$toast(res.message);
        }
      });
    },
    getYaoQing() {
      var url = window.location.href; //
      if (url.indexOf("InviteCode") != -1) {
        console.log(99999);
        let newurl = url.split("InviteCode=")[1].split("#")[0];
        this.InviteCode = newurl;
      }
      // var dz_url = url.split("#")[1];
      // var cs = dz_url.split("?")[1];
      // var cs_arr = cs.split("=")[1];
      // console.log(cs_arr);
    },
    selectBox() {
      this.checkTyBtn = !this.checkTyBtn;
    },
    beforeClose(action, done) {
      if (action === "confirm") {
        if (this.imageCode) {
          // done(false); //不关闭弹框
          done();
          this.setCode();
        } else {
          this.$toast(this.i18n.inputCode); //"请输入验证码"
          done(); //关闭
        }
      } else if (action === "cancel") {
        done(); //关闭
      }
    },
    setCode() {
      console.log(2);
      // uni.showLoading({
      //   title: "加载中..."
      // });
      //  this.contontsA = res.data;

      let _data = {
        imageToken: this.imageToken,
        imageCode: this.imageCode,
        accountNumber: this.user.Name,
      };
      console.log(_data);
      sendSmsCode(_data).then((res) => {
        // console.log(res);
        this.bured = true;
        this.show = false;
        if (res.data.status == 1) {
          var timeCount = 60;
          if (!this.timer) {
            this.count = timeCount;
            // console.log(this.count);
            //  this.show = false;
            this.timer = setInterval(() => {
              if (this.count > 0 && this.count <= timeCount) {
                this.count--;
                this.CountMessage = "剩余" + this.count + "s";
              } else {
                //  this.show = true;
                // this.dis = false;
                clearInterval(this.timer);
                this.CountMessage = "重新获取";
                this.bured = false;
                this.timer = null;
              }
            }, 1000);
          }
        } else {
          this.bured = false;
          this.CountMessage = "重新获取";
          this.$toast(res.data.message);
        }
      });
    },
    get_message_code() {
      //  alert(2)
      var strTemp = /^1[3|4|5|6|7|8|9][0-9]{9}$/;
      if (strTemp.test(this.user.Name)) {
        // this.imgYan();
        this.gettucode();
      } else {
        this.$toast("请核对您的手机号码");
        // uni.showToast({
        //   title: "请核对您的手机号码",
        //   icon: "none"
        // });
      }
    },
    gettucode() {
      // this.show = true;
      getImageCode().then((res) => {
        console.log(res);
        if (res.status == 200) {
          if (!res.data.status) {
            this.imger = res.data.code;
            this.imageToken = res.data.codeToken;
          } else {
            this.$toast(res.data.message);
          }
        }
      });
    },
    getDownUrl() {
      getDownUrl().then((res) => {
        console.log(res);
        if (res.status == 1) {
          this.downLoadUrl = res.data;
        } else {
          this.$toast(res.message);
        }
      });
    },
    //检测app权限模块结束
    //上传通讯录
    postPhone(data) {
      let _data = {
        mobileNumber: this.mobileNumber,
        mailList: data,
      };
      getUserContact(_data).then((res) => {
        // alert("成功了");
      });
    },
    submit_to_login() {
      // alert(1);
      this.$router.push({
        path: "/home?t=" + Date.now(),
        query: {
          t: Date.now(),
        },
      });
    },
    //发送验证码
    getSms() {},
  },
};
</script>
<style type="text/scss" lang="scss" scoped>
.dealRecord-wrap {
  font-size: 14px;

  height: 100vh;
  width: 100%;
  box-sizing: border-box;
  // background: image('../assets/images/registorBg.png');
}
// .page-bg {
//   position: absolute;
//   top: 0;
//   left: 0;
//   z-index: 1;
// }

.section-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: 330px 40px 0;
  box-sizing: border-box;
  // margin-top: 5rem;

  .language-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0rem 20px 44px;
    .item-wrap {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      box-sizing: border-box;
      font-size: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .input-wrap {
    height: 70px;
    width: 100%;
    background: rgba(0, 0, 0, 0.37);
    border-radius: 35px;
    padding: 0 24px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;

    input {
      background: none;
      outline: none;
      border: 0px;
      font-size: 22px;
      width: 100%;
      outline: none;
      color: #ffffff;
      // background: rgba(255, 255, 255, 0.31);
    }
    input::-webkit-input-placeholder {
      color: #ffffff;
    }
  }
  .input-margin-top {
    margin-top: 48px;
  }
  .registor-btn-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 22px;
    .btn {
      width: 240px;
      height: 80px;
      text-align: center;
      line-height: 80px;
      font-weight: bold;
      font-size: 32px;
      color: #ffffff;
      border-radius: 40px;

      background: linear-gradient(90deg, #ffcdac, #e46ec4);
      box-shadow: 0px 12px 7px 0px rgba(68, 32, 81, 0.17);
    }
  }
}
.fixed-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  background: rgba($color: #000000, $alpha: 0.5);
}
.head-tabindex {
  background: linear-gradient(90deg, #ffcdac, #e46ec4);
  color: #ffffff;
}
.no-head-tabindex {
  border: 2px solid #313131;
  box-shadow: 0px 11px 12px 0px rgba(51, 39, 81, 0.17);
  color: #1b1b1b;
}
.get-code-style {
  font-size: 22px;
  height: 70px;
  width: 170px;
  text-align: center;
  line-height: 70px;
  background: linear-gradient(90deg, #ffcdac, #e46dc4);
  border-radius: 35px;
  color: #ffffff;
}
.down-text-style {
  margin-top: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
  .text-padding {
    padding: 28px;
    font-size: 20px;
  }
}
.padding-top-one {
  padding-top: 63px;
  img {
    width: 100px;
    height: 100px;
  }
}
.padding-top-two {
  padding-top: 32px;
  img {
    width: 300px;
    height: 32px;
  }
}
.padding-top-three {
  padding-top: 28px;
}
</style>



