import axios from 'axios'
// import { Toast } from 'vant'
// const baseURL = 'http://hemabnd.chnsxhc.com/hemapai/'

// axios.defaults.baseURL = baseURL
// const baseURL = '/'
const baseURL = 'https://www.uueden.com'//https://www.uueden.com
//http://111.229.198.81:83  http://192.168.1.11:8888

axios.defaults.baseURL = baseURL
//设置默认请求头
console.log('语言')//localStorage.getItem('lang')
axios.defaults.headers = {
	'X-Requested-With': 'XMLHttpRequest',

}
axios.defaults.timeout = 10000
// http://hema.chnlqz.com/hemapai/wallet/system/thirdSendSmsCode

// respone拦截器
axios.interceptors.response.use(
	response => {
		/**
		 * code为非1是抛错 
		 */
		const res = response
		console.log(res)
		if (res.status !== 200) {
			// 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
			// if (res.code === 1000 || res.code === 5004 || res.code === 50014) {
			// 	Toast.fail('登录超时，请重新登陆')
			// 	router.push('/login')
			// 	return Promise.reject(response.data)
			// }
			Toast({
				type: 'fail',
				message: res.message,
				duration: 2000
			});
			return Promise.reject(res)
		} else {
			return res;
		}
	},
	error => {
		//		setTimeout(res => {
		// Toast({
		// 	type: 'fail',
		// 	message: '请求超时',
		// 	duration: 2000
		// });
		//		}, 0)
		return Promise.reject(error)
	}
)

export const getImageCode = params => {
	axios.defaults.headers.lang = localStorage.getItem("lang");
	return axios.post('/user/getImageCode', params).then(res => res)
}
export const sendSmsCode = params => {
	axios.defaults.headers.lang = localStorage.getItem("lang");
	return axios.post('/uu/user/sendSmsCode', params).then(res => res.data)
}
// POST 
export const register = params => {
	axios.defaults.headers.lang = localStorage.getItem("lang");
	return axios.post('/uu/user/registerH5', params).then(res => res.data)
}
export const getDownUrl = params => {
	axios.defaults.headers.lang = localStorage.getItem("lang");
	return axios.post('/h5/getDownUrl', params).then(res => res.data)
}



